.additional-goods {
  .border-green;
}

.additional-goods__wrapper {
  margin-bottom: 38px;
  padding-left: 20px;
  padding-right: 20px;

  border-top: 1px solid #e6e6e6;
}

.additional-goods__rubber-wrapper {
  .wrapper;
}

.additional-goods__title {
  width: 265px;
  .zero;
  margin-top: -15px;
  padding-bottom: 35px;
  padding-left: 20px;

  font-family: @oswaldr;
  .size(24px; 24px);
  font-weight: normal;

  color: #111111;
  background-color: #ffffff;
}

.additional-goods__list {
  .zero;

  list-style: none;
}

.additional-goods__item-title {
  .zero;
  padding-bottom: 13px;

  font-family: @oswaldr;
  .size(16px; 16px);
  font-weight: normal;
  text-transform: uppercase;

  color: #222222;
}

.additional-goods__table {
  width: 100%;
  margin-bottom: 15px;

  line-height: 14px;

  border-collapse: collapse;
}

.additional-goods__price {
  text-align: right;
}

.additional-goods__item {
  padding-top: 13px;

  .border;
}

.additional-goods__button {
  width: 280px;
  margin-bottom: 18px;
  padding-top: 10px;
  padding-bottom: 10px;

  .size(16px; 20px);
}

@media (min-width: @tablet-width) {
  .additional-goods__rubber-wrapper {
    .tablet-wrapper;
  }

  .additional-goods__wrapper {
    margin-bottom: 70px;
    padding-left: 30px;
    padding-right: 30px;

    border: none;
  }

  .additional-goods__title {
    width: 413px;
    margin-top: -26px;
    margin-left: 28px;
    margin-bottom: 47px;
    padding-left: 40px;

    .size(36px; 36px);
  }

  .additional-goods__item-title {
    width: 200px;

    .size(20px; 20px);
  }

  .additional-goods__table {
    width: 230px;
    margin-top: 9px;

    .size(16px; 20px);
  }

  .additional-goods__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 20px 35px 27px 38px;

    border: 1px solid #e6e6e6;
    border-bottom: none;
  }

  .additional-goods__item:last-of-type {
    border: 1px solid #e6e6e6;
  }

  .additional-goods__button {
    width: 632px;
    margin: 0;
    margin-top: 7px;
    padding-bottom: 10px;
  }

  .additional-goods__quantity {
    width: 135px;
    text-align: right;
  }
}

@media (min-width: @desktop-width) {
  .additional-goods {
    min-width: 1300px;
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
  }

  .additional-goods__rubber-wrapper {
    display: flex;
    flex-wrap: wrap;

    width: 1300px;
    margin: 0 auto;
  }

  .additional-goods__title {
    margin-left: 0;
  }

  .additional-goods__wrapper {
    padding-left: 40px;
    margin-right: 50px;
    margin-bottom: 85px;
  }

  .additional-goods__list {
    width: 895px;
  }

  .additional-goods__table {
    width: 300px;
    margin-bottom: 0;
  }

  .additional-goods__item {
    flex-wrap: nowrap;
    padding: 16px 0 15px 0;

    border-right: none;
    border-left: none;
  }

  .additional-goods__item:last-of-type {
    border-right: none;
    border-left: none;
  }

  .additional-goods__item-title {
    margin-right: 100px;
    padding: 0;
  }

  .additional-goods__quantity {
    width: 220px;
    text-align: left;
  }

  .additional-goods__price {
    width: 50px;

    text-align: left;
  }

  .additional-goods__button {
    width: 245px;
    margin: 0;
  }
}
